: 0;\n      max-width: 100%;\n      position: fixed;\n      z-index: 1030;\n      border-radius: 0;\n      padding: 30px 20px;\n      height: 100%;\n    }\n\n    #{$root} {\n      &__phone,\n      &__address {\n        max-width: 100%;\n        padding: 0 0 0 40px;\n        margin-bottom: 20px;\n      }\n      &__phone-link {\n        color: #FFF;\n      }\n      &__address-text {\n        font-size: 17px;\n        color: #FFF;\n      }\n      &__address-ico,\n      &__phone-ico {\n        fill: #FFF;\n      }\n    }\n  }\n\n  &__popup-title {\n    color: #FFF;\n    font-weight: bold;\n    font-size: 26px;\n    padding: 0 25px 25px 0;\n  }\n\n  &__popup-btn {\n    position: absolute;\n    width: 20px;\n    height: 20px;\n    top: 35px;\n    right: 40px;\n  }\n\n  &__popup-close {\n    fill: #fff;\n    width: 100%;\n    height: 100%;\n  }\n\n  &__social {\n    width: 100%;\n    padding: 0 5px;\n  }\n\n  &__social-list {\n    margin: 0 -5px;\n    display: flex;\n    align-items: center;\n  }\n\n  &__social-link {\n    border-radius: 5px;\n    width: 100%;\n    max-width: calc(16.666% - 10px);\n    margin: 0 5px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    position: relative;\n    background: $dark2;\n    transition: background .3s ease;\n\n    &:hover {\n      background: $shadow-dark2;\n    }\n\n    &::after {\n      content: \"\";\n      display: block;\n      padding-top: 100%;\n    }\n\n    @media(max-width: 767px) {\n      max-width: 40px;\n    }\n  }\n\n  &__social-ico {\n    fill: #FFF;\n    max-width: 20px;\n    max-height: 21px;\n    position: absolute;\n  }\n\n  &__btn {\n    margin-top: 30px;\n    display: inline-flex;\n    align-items: center;\n    justify-content: center;\n    padding: 11px 20px 11px;\n    font-size: 15px;\n    color: #FFF;\n    font-weight: 500;\n    border-radius: 10px;\n    background: $dark2;\n    transition: background .3s ease;\n\n    &:hover, &:focus {\n      background: $shadow-dark2;\n    }\n 